/* üìú Left Panel: Goal List */
.goal-tree {
    width: 280px;
    border-right: 2px solid #ddd;
    padding-right: 20px;
}

.goal-tree h2 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.goal-list-header-container {
    display: inline-block;
    // color: white;
    // flex-direction: column;
    // width: auto;
}

.goal-list-title {
    margin: 20px 20px 0px 20px;
    font-size: large;
    font-weight: 500;
}

.goal-subtitle {
    font-size: small;
    font-weight: 500;
    margin-left: 20px;
    margin-bottom: 5px;
    // border-bottom: 1px solid black;
    display: inline-block;
}

.goal-tutorial {
    font-size: medium;
    margin-top: 10px;
    margin-left: 20px;
    margin-bottom: 5px;
    font-style: italic;
    color: grey;
    // display: inline-block;
}

.goal-list {
    list-style: none;
    padding: 0;
}

.goal-list li {
    cursor: pointer;
    padding: 12px;
    margin-bottom: 5px;
    border-radius: 5px;
    transition: background 0.2s;
    background: #f9f9f9;
}

.goal-list li:hover {
    background: #f0f0f0;
}

.goal-list .selected {
    background: #007bff;
    color: white;
}

/* ü§ñ AI Breakdown Button */
.ai-btn {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s;
}

.ai-btn:hover {
    background: #0056b3;
}

/* üìù Right Panel: Kanban Board */
.kanban-board {
    flex: 1;
    padding-left: 20px;
}

.kanban-board h2 {
    font-size: 1.5rem;
}

/* Ticket Styling */
.ticket-list-title {
    margin-top: 20px;
    font-size: large;
    font-weight: 500;
    padding-left: 10px;
    // color: #ffcec5;
    font-weight: 600;
}

.subtitle {
    font-size: small;
    margin-top: 5px;
    margin-bottom: .5rem;
    padding-left: 10px;
    font-weight: 500;
    // border-bottom: 1px solid black;
}

.ticket-list-container {
    display: flex;
    flex-direction: column;
    min-width: 25vw;
    min-height: 100vh;
    padding-left: .7rem;
}

.ticket-tutorial {
    font-size: medium;
    margin-left: 20px;
    margin-bottom: 5px;
    margin-right: 5px;
    font-style: italic;
    color: grey;
    // display: inline-block;
}

.ticket-list-spaceholder {
    border: 1px solid transparent;
    min-height: 400px;
}

.open-tickets-selector, .closed-tickets-selector {
    display: flex;
    // justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    img {
        height: 15px;
        margin-right: 5px;
    }
    &:hover {
        cursor: pointer;
    }
    .focus-selector {
        // position: sticky;
        // right: 0;
        font-size: x-small;
        border: 1px solid #00dede;
        color: black;
        margin-left: .5rem;
        border-radius: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
        background: whitesmoke;
        svg {
            color: #00dede;
        }
    }
    .focus-enabled {
        background-color: #00dede;
        svg {
            color: black;
        }
    }
}

.ticket-card {
    padding: 5px 10px;
    padding-left: 20px;
    margin-right: 10px;
    margin-bottom: 8px;
    cursor: grab;
    transition: 0.2s;
    font-size: .9rem;
    max-width: 75%;
    overflow: visible;
}

.selected-ticket-card {
    background: #f0f0f020 !important;
}

.ticket-card:hover {
    background: #f0f0f0;
    color: black;
}

.ticket-card:active {
    cursor: grabbing;
}

.ticket-context-container {
    position: fixed;
    border-radius: 6px;
    padding: 10px;
    top: 0;
    right: 0;
    background: #ececec;
    // height: 10vh;
    width: 20vw;
    z-index: 100;
    display: flex;
    flex-direction: column;

    .ticket-context-action {
        margin-bottom: 5px;
        padding: 5px;
        &:hover {
            cursor: pointer;
            background: white;
        }
    }

    svg {
        margin-right: 10px;
    }
}

.ticket-context-container.fade-in {
    opacity: 1;
    transform: scale(1);
    transition: opacity 120ms ease, transform 120ms ease;
}

.ticket-context-container.fade-out {
    opacity: 0;
    transform: scale(0.95);
    transition: opacity 500ms ease, transform 120ms ease;
}

@media (hover: none) {
    .ticket-card:hover {
        color: inherit;
        background: inherit;
    }

    .ticket-card {
        user-select: none;
        /* Prevents annoying text selection on long press */
        -webkit-user-select: none;
        /* Safari */
        -moz-user-select: none;
        /* Firefox */
        -ms-user-select: none;
        /* IE */
    }
}

.ticket-card.ghosted {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.15);
    color: black;
    padding: 5px 10px;
    padding-left: 20px;
    margin-right: 10px;
    margin-bottom: 8px;
    cursor: grab;
    transition: 0.2s;
    font-size: 0.9rem;
    width: 70vw;
}

.goal-and-ticket-container {
    display: flex;
    flex-direction: column;
    width: 80vw;
}

.goals-container {
    display: flex;
    flex-direction: row;
}

.goal-selection {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 10px;
    gap: 10px;
}

.dark-mode .goal-toggle {
    background-color: #212121;
    color: white;
}

.goal-toggle {
    display: flex;
    align-items: center;
    // font-family: Nunito;
    padding: 7px 10px;
    font-size: 1rem;
    border: 1px solid #3695de77;
    border-radius: 5px;
    // background-color: whitesmoke;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.is-bucket {
    border: 1px solid #dea33677;
}

.goal-toggle:hover {
    background-color: #ddd;
}

.goal-toggle.selected {
    background-color: #3694de;
    color: white;
    border-color: #3694de;
}

.is-bucket.selected {
    color: black;
    background-color: #dea336;
    border-color: #dea336;
}

.kanban-board {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    overflow-x: auto;
}

.kanban-columns {
    display: flex;
    gap: 15px;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
}

@media (max-width: 768px) {
    .ticket-list-container {
        // width: 100vw;
        min-height: unset;
    }

    .ticket-list-spaceholder {
        min-height: 60vh;
    }

    .goal-and-ticket-container {
        width: 100vw;
    }

    .goals-container {
        flex-direction: column-reverse;
    }

    .ticket-context-container {
        min-width: 250px;
    }

}